const animItems = document.querySelectorAll('.anim-item');

if (animItems.length > 0) {
	window.addEventListener('scroll', animOnScroll);

	function animOnScroll(params) {
		for (let index = 0; index < animItems.length; index++) {
			const animItem = animItems[index];
			const animItemHeight = animItem.offsetHeight;
			const animItemWidth = animItem.offsetWidth;
			const animItemOffsetTop = offset(animItem).top;
			const animItemOffsetLeft = offset(animItem).left;
			const animStart = 3;

			let animItemPointY = window.innerHeight - animItemHeight / animStart;
			let animItemPointX = window.innerWidth - animItemWidth / animStart;

			if (animItemHeight > window.innerHeight) {
				animItemPointY = window.innerHeight - window.innerHeight / animStart;
			} else if (animItemWidth > window.innerWidth) {
				animItemPointX = window.innerWidth - window.innerWidth / animStart;
			}

			if(pageYOffset > animItemOffsetTop - animItemPointY && pageYOffset < animItemOffsetTop + animItemHeight && pageXOffset > animItemOffsetLeft - animItemPointX && pageXOffset < animItemOffsetLeft + animItemWidth) {
				animItem.classList.add('active');
			} else {
				if (!animItem.classList.contains('anim-item--no-hide')) {
					animItem.classList.remove('active');
				}
			}
		}
	}

	function offset(el) {
		const rect = el.getBoundingClientRect(),
		scrollLeft = window.pageXOffset || document.documentElement.scrollLeft,
		scrollTop = window.pageYOffset || document.documentElement.scrollTop;
		return {
			top: rect.top + scrollTop,
			left: rect.left + scrollLeft
		}
	}

	setTimeout(() => {
		animOnScroll();
	}, 300);
}

$(window).scroll(function(){
	// hide consulting
	if ($(this).scrollTop() >= 400) {
		$(".consulting").slideUp(600)
	}

	// parallax bg
	let v = $(window).height();
	let s = $(window).scrollTop();

	if($('.about-scope').length){
		let h = $('.about-scope').height();
		let begin = $('.about-scope').offset().top + h - v;
		let end = $('.about-scope').offset().top;
		
		if (s > begin && s < end)
		{
			var o = ((s - begin) / (end - begin) * 100) + '%';
		}

		$('.about-scope').css('background-position-y', o);
	}

	// to top
	if ($(this).scrollTop() >= 1200) {
		$(".to-top").addClass('active')
	} else {
		$(".to-top").removeClass('active')
	}
});

var history = document.querySelector('.history');
var historyScroll = document.querySelector('.history-scroll');

if (history) {
	var horizontalScroll = function (scrollWrapper, scrollItem) {
		// Получаем позиции элемента
		var scrollWrapperPosition = {
			top: window.pageYOffset + scrollWrapper.getBoundingClientRect().top,
			bottom: window.pageYOffset + scrollWrapper.getBoundingClientRect().bottom
		},
		// Получаем позиции элемента
		scrollItemSize = {
			width: scrollItem.getBoundingClientRect().width,
			height: scrollItem.getBoundingClientRect().height
		},
		  // Получаем позиции окна
		windowPosition = {
			top: window.pageYOffset,
			bottom: window.pageYOffset + document.documentElement.clientHeight
		};
	
		$(scrollWrapper).css('height', scrollItemSize.width + 'px');
	
		var scrollWrapperPercent = ($(window).scrollTop() - scrollWrapperPosition.top) / (scrollItemSize.width - scrollItemSize.height);
		var scrollWrapperPercentFix = scrollWrapperPercent * 100 * (scrollItemSize.width - document.documentElement.clientWidth) / scrollItemSize.width;
	
		if (scrollWrapperPosition.top <= windowPosition.top) {
			$(scrollItem).removeClass('start');
			$(scrollItem).addClass('fixed');
			$(scrollItem).css({
				'transform': 'translateX(-' + scrollWrapperPercentFix + '%)',
				'-webkit-transform': 'translateX(-' + scrollWrapperPercentFix + '%)'
			});
		} else {
			$(scrollItem).addClass('start');
			$(scrollItem).removeClass('fixed');
		};
		if (scrollWrapperPosition.bottom < windowPosition.bottom) {
			$(scrollItem).addClass('end');
		} else {
			$(scrollItem).removeClass('end');
		}
	};

	// Запускаем функцию при прокрутке страницы
	window.addEventListener('scroll', function() {
		horizontalScroll(history, historyScroll);
	});

	setTimeout(() => {
		horizontalScroll(history, historyScroll);
	}, 300);
}

$('a[href^="#"]').click(function () { 
	let elementClick = $(this).attr("href").replace(/#/, '');
	if (elementClick && $('a[name='+elementClick+']').length)
	{
		let destination = $('a[name='+elementClick+']').offset().top;
		$('html, body').animate( { scrollTop: destination }, 1100 );
		return false;
	}
});

