$('.career-vacancy__item:nth-of-type(odd) .career-vacancy__item-icon').click(function(){
	$('.career-vacancy__item-icon').removeClass('active')
	$(this).addClass('active')

    $('.career-vacancy__wrapper').removeClass('active-right');
	$('.career-vacancy__wrapper').addClass('active-left');

	$('.career-vacancy__popup').slideUp(600);
	$(this).closest('.career-vacancy__list').prev('.career-vacancy__col').children('.career-vacancy__popup').slideDown(600);

	setTimeout(() => {
		let destination = $(this).closest('.career-vacancy__list').prev('.career-vacancy__col').children('.career-vacancy__popup').offset().top;
		$('html, body').animate( { scrollTop: destination - 40 }, 600 );
	}, 600);
});

$('.career-vacancy__item:nth-of-type(even) .career-vacancy__item-icon').click(function(){
	$('.career-vacancy__item-icon').removeClass('active')
	$(this).addClass('active')

    $('.career-vacancy__wrapper').removeClass('active-left');
	$('.career-vacancy__wrapper').addClass('active-right');

	$('.career-vacancy__popup').slideUp(600);
	$(this).closest('.career-vacancy__list').next('.career-vacancy__col').children('.career-vacancy__popup').slideDown(600);

	setTimeout(() => {
		let destination = $(this).closest('.career-vacancy__list').next('.career-vacancy__col').children('.career-vacancy__popup').offset().top;
		$('html, body').animate( { scrollTop: destination - 40 }, 600 );
	}, 600);
});

$('.career-vacancy__popup-close').click(function(){
	$(this).closest('.career-vacancy__popup').slideUp(600);
	$('.career-vacancy__item-icon').removeClass('active')
	$('.career-vacancy__wrapper').removeClass('active-left');
	$('.career-vacancy__wrapper').removeClass('active-right');
});

