const marqueeItems = document.querySelectorAll('.marquee-item');
if (marqueeItems.length > 0) {
	function animateMarquee() {
		for (let index = 0; index < marqueeItems.length; index++) {
			const el = marqueeItems[index];
			const innerEl = el.children[0];
			const innerWidth = innerEl.offsetWidth;
			const dataSpeed = el.getAttribute('data-marque-speed');
			const duration = dataSpeed * 1000;
			const cloneEl = innerEl.cloneNode(true);
			el.appendChild(cloneEl);

			let start = performance.now();
			let progress;
			let translateX;

			requestAnimationFrame(function step(now) {
				progress = (now - start) / duration;
		 
				if (progress > 1) {
					progress %= 1;
					start = now;
				}
		
				translateX = innerWidth * progress;
				
				innerEl.style.transform = `translateX(-${translateX}px)`;
				cloneEl.style.transform = `translateX(-${translateX}px)`;
				requestAnimationFrame(step);
		  });
		}
	}

	animateMarquee();
}

